* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.Star {
  all: unset;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  display: inline-block;
  height: 2em;
  margin: 0.2em;
  width: 2em;
}

.Star__icon {
  height: 100%;
  width: 100%;
}

.RatingsCard {
  cursor: pointer;
  /* Filter over box-shadow to support the flip effect */
  -webkit-filter: drop-shadow(0 3px 6px rgba(0, 0, 0, 0.16))
    drop-shadow(0 3px 6px rgba(0, 0, 0, 0.1));
          filter: drop-shadow(0 3px 6px rgba(0, 0, 0, 0.16))
    drop-shadow(0 3px 6px rgba(0, 0, 0, 0.1));
  height: 20rem;
  position: relative;
  -webkit-perspective: 1000px;
          perspective: 1000px;
  transition: -webkit-filter 0.5s;
  transition: filter 0.5s;
  transition: filter 0.5s, -webkit-filter 0.5s;
  width: 20rem;
}

.RatingsCard:hover {
  -webkit-filter: drop-shadow(0 14px 28px rgba(0, 0, 0, 0.25))
    drop-shadow(0 10px 10px rgba(0, 0, 0, 0.1));
          filter: drop-shadow(0 14px 28px rgba(0, 0, 0, 0.25))
    drop-shadow(0 10px 10px rgba(0, 0, 0, 0.1));
}

.RatingsCard__front,
.RatingsCard__back {
  background-color: rgb(230, 230, 230);
  height: 100%;
  position: absolute;
  width: 100%;
}

.RatingsCard__front {
  background-position: center;
  background-size: cover;
}

.RatingsCard__back {
  -webkit-align-items: center;
          align-items: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}

.App {
  -webkit-align-items: center;
          align-items: center;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
  grid-gap: 2rem;
  justify-items: center;
  min-height: 100vh;
  padding: 2rem;
}

